@import url(https://fonts.googleapis.com/css?family=Space+Mono:400,700);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200;300;400;600;700;800;900&family=Roboto:wght@200;300;400;600;700;800;900&display=swap);
h1{margin:0.5rem;margin-left:0;font-size:2rem}.nav ul{list-style-type:none;margin:0;padding:0;overflow:hidden}.nav ul li{float:left;font-weight:700;text-decoration:none !important}.nav ul li a{display:block;text-align:right;padding:1rem;font-weight:300;text-decoration:none;color:#4b4b4b !important}@media only screen and (max-width: 768px){.nav ul li a{padding:1rem 0.75rem}}.nav ul li a:hover{font-weight:700}.nav ul li a.active{font-weight:700;text-decoration:none !important}

#intro{color:#4b4b4b;background-color:#fff;margin-top:0.5rem;text-align:center}#intro ul{list-style-position:inside}#intro ul li{font-size:1rem;line-height:1.125rem;margin-bottom:1.125rem;align-self:center}#intro h2.first{margin:40vh auto}#intro h2,#intro h3,#intro h4,#intro p{text-align:center}#intro .col{margin:0 3rem !important}#intro img{width:100%}#intro button:focus{outline:0}#intro button{background-color:#fff;color:#4b4b4b;font-size:1.125rem;font-family:"Nunito Sans", sans-serif;padding:1rem 1.5rem;margin-bottom:1rem;border:1px solid rgba(0,0,0,0.1);margin-right:0.5rem;transition:color 0.25s ease, background-color 0.25s ease, border-color 0.25s ease}#intro button a{text-decoration:none}#intro button:hover{border-color:#ff4500}#intro button.is-checked{color:#fff;background-color:#4b4b4b}@media only screen and (max-width: 768px){#intro button{border-color:#ff4500}}#intro-inverse{width:100%;margin:4rem auto;background-color:#fff;color:#4b4b4b}#intro-inverse p,#intro-inverse h3{margin-bottom:2rem}

.article-info{margin:1rem auto;padding-bottom:2rem}.article-info img{width:100%;margin:0 auto;display:block;transition:0.1s ease-in-out all;box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box;border:1px solid #fff;border-radius:8px}.article-info h4{font-size:1.125rem;margin-top:1rem;margin-bottom:1rem;font-weight:600}.article-info h4.secondary{margin-bottom:0.25rem;font-weight:400;font-size:1rem;margin-top:0.5rem}.article-info svg{vertical-align:top;margin-right:0.333rem}.article-info p{font-size:1rem;margin-top:1rem;padding-bottom:0.5rem}.article-info a,.article-info a:visited{color:#ff4500;text-decoration:none}.article-info a:hover{color:#ff4500;text-decoration:underline}

.article-open{background:#ffffff;min-height:80vw;height:100%;width:100%;margin:0 auto;padding:0}.article-open h2{font-size:1.75rem;margin-top:0.5rem;margin-bottom:0.5rem}.article-open img.article-photo{display:block;max-width:100%;max-height:75vh;height:auto;width:auto;margin:0 auto}.article-open h3,.article-open p{margin-bottom:0.5rem}.article-open h3{margin-top:2rem}.article-open h4{margin:1rem 0}.article-open svg{vertical-align:top;margin-right:0.333rem}.article-open ul{width:100%;padding-left:0.5rem;list-style:inside none}@media only screen and (min-width: 400px){.article-open{width:100%;padding:1rem}.article-open .icon-and-info{text-align:right;margin-left:20%}}button.submit{background-color:#7fffd4;color:#4b4b4b;font-size:1rem;font-family:"Nunito Sans", sans-serif;font-weight:600;padding:1rem 3rem;margin-bottom:1rem;border-radius:0.5rem;margin-right:0.5rem}button.reset{background-color:#fff;color:#4b4b4b;padding:1rem 2rem;margin-bottom:1rem;border-radius:0.5rem;margin-right:0.5rem}button.submit:hover,button.reset:hover{color:#fff;background-color:#4b4b4b}textarea{min-height:8rem}

.program{padding:1rem;display:block;overflow:auto}.program .filters{margin:2rem auto;text-align:center}@media only screen and (min-width: 767px){.program .filters{margin:4rem auto}}.program .filters .buttons{display:inline-block}.program .filters .buttons button:focus{outline:0}.program .filters .buttons button{background-color:#7fffd4;color:#4b4b4b;font-size:1rem;font-family:"Nunito Sans", sans-serif;font-weight:600;padding:1rem 1.5rem;margin-bottom:1rem;border-radius:0.5rem;margin-right:0.5rem}.program .filters .buttons button:hover{color:#fff;background-color:#4b4b4b}.program .filters .buttons button.is-checked{color:#fff;background-color:#4b4b4b}.program .filters .buttons button.styles{color:#4b4b4b;background-color:#fff;padding:1rem;border-radius:0;padding-bottom:0.5rem;margin-bottom:1rem}.program .filters .buttons button.styles:hover{border-bottom:3px solid #7fffd4}.program .filters .buttons button.styles.is-checked{border-bottom:3px solid #4b4b4b}.program h4{color:#222}.program p,.program h4.secondary{color:#4b4b4b}.my-masonry-grid{display:flex;margin-left:-2rem;width:auto}.my-masonry-grid_column{padding-left:2rem;background-clip:padding-box}

.about h2{margin-bottom:2rem;margin-top:4rem;color:#222}.about h3{margin-top:2rem}.about h3,.about p{margin-bottom:1.25rem;color:#4b4b4b}.about p{line-height:1.75rem;font-size:1.125rem}.about a:link{text-decoration:none;color:#ff4500}.about a:hover{text-decoration:underline;color:#ff4500}.about ul{padding-top:0.5rem}.about ul li{line-height:1.75rem;font-size:1.125rem;margin-left:1.5rem;margin-bottom:1.5rem;color:#4b4b4b}

footer{margin-top:4rem;border-top:1px solid rgba(0,0,0,0.1);color:#4b4b4b;background:#fff;padding:4rem}footer p{margin:2rem 0}footer a:link{color:#ff4500;text-decoration:underline}

*{padding:0;margin:0;border:0;box-sizing:border-box}body{font-family:"Nunito Sans", sans-serif;font-size:16px}h1,h2,h3{color:#222;font-weight:600}a{color:#222}h4,h5,p,li{color:#4b4b4b;font-weight:300}p{font-family:"Nunito Sans", sans-serif}.container{width:100%;max-width:1200px;margin:0 auto;padding:0}.highlighted{color:#ff4500 !important}a.pinklink{text-decoration:none;color:#ff4500}a.pinklink:hover{text-decoration:underline}

